import React, { Component } from 'react';
import './About.css';

export default class About extends Component {
    render() {
        return (
            <div className="About">
                <div className="container">
                    <div className="title">
                        About us.
                    </div>
                    <div className="description">
                        Creators deserve to be rewarded for their hard work. Algorithm Songs 
                        strives to help you combat monetization issues and grow your brand 
                        in the process. We work hard to bring our creators paid promotions for 
                        the most trending copyright-free songs.
                    </div>
                </div>
            </div>
        )
    }
}
