import React, { Component } from 'react';
import './Discord.css';

export default class Discord extends Component {
    render() {
        return (
            <div className="Discord">
                <div className="container">
                    <div className="title">
                        Join our discord.
                    </div>
                    <div className="description">
                        Join our community to receive updates and connect with other
                        Algorithm Songs talent. Our growing community is a great resource
                        for networking and building your brand.
                    </div>
                </div>
                <div className="icon">
                    <a href="https://discord.gg/C7bEw2ZS6d" target="_blank" rel="noreferrer">
                        <div className="image"></div>
                    </a>
                </div>
            </div>
        )
    }
}
