import React, { Component } from 'react';
import './Socials.css';

export default class Socials extends Component {
    render() {
        return (
            <div className="Socials">
                <div className="container">
                    <div className="title">
                        Follow us on Twitter.
                    </div>
                    <div className="description">
                        Follow us for updates on promotions and new releases from our
                        creators.
                    </div>
                </div>
                <div className="icon">
                    <a href="https://twitter.com/algorithmsongs" target="_blank" rel="noreferrer">
                        <div className="image"></div>
                    </a>
                </div>
            </div>
        )
    }
}
